.menu-open {
  width: 20px;
  height: 2px;
  background-color: var(--primal-text-color);
  border-radius: 1px;
  transition: background-color 1s;
  position: relative;
}

.menu-open:before {
  content: "";
  height: 2px;
  width: 100%;
  background-color: var(--primal-text-color);
  transform: rotate(0);
  transform-origin: 0;
  border-radius: 1px;
  transition: transform .5s;
  display: block;
  position: absolute;
  top: 6px;
}

.menu-open:after {
  content: "";
  height: 2px;
  width: 100%;
  background-color: var(--primal-text-color);
  transform: rotate(0);
  transform-origin: 0;
  border-radius: 1px;
  transition: transform .5s;
  display: block;
  position: absolute;
  top: -6px;
}

.search-form {
  border-radius: 15px;
  padding: 4px 12px;
  transition: all .5s;
  display: flex;
  transform: translate(143px);
}

#search-area {
  background-color: inherit;
  width: 100%;
  margin-left: 5px;
}

.search-thumb {
  width: 173px;
  height: 31px;
  align-items: center;
  margin-right: 14px;
  transition: all .5s;
  display: flex;
  overflow: hidden;
}

.menu-thumb {
  height: 14px;
  align-items: center;
  display: flex;
}

.active-menu:after {
  transform-origin: 0;
  transition: transform .5s;
  transform: rotate(37deg);
}

.active-menu:before {
  transform-origin: 0;
  transition: transform .5s;
  transform: rotate(-37deg);
}

.active-menu {
  background-color: rgba(0, 0, 0, 0);
  transition: background-color .25s;
}

.modal-mobile-menu {
  width: 420px;
  height: 100%;
  background-color: #0ff;
  transition: all .5s;
  position: fixed;
  top: 80px;
  left: 50%;
  transform: translate(-50%);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: all .5s;
}

